import React from 'react';
import {S} from "components/notFound/NotFound_Styles"
import notFound from "assets/notFound.png"

type PropsType = {
    handleResetFilters: () => void
}
export const NotFound: React.FC<PropsType> = ({handleResetFilters}) => {
    return (
        <S.MainContainer>
            <S.ContentContainer>
                <img src={notFound} alt={'Man is trying to find something'}/>
                <S.NotFoundTitle>     </S.NotFoundTitle>
                <S.Description>   
                        </S.Description>
                <S.ClearBtn onClick={handleResetFilters}> </S.ClearBtn>
            </S.ContentContainer>
        </S.MainContainer>
    );
};


